import { MAT_DIALOG_DATA } from '@angular/material';
import { Component, Inject } from '@angular/core';

@Component({
    selector: 'ee-facturacion-dialog',
    template: require('./ee-facturacion-dialog.component.html')
})
export class EeFacturacionDialogComponent {
    dataSourceServicios;
    dataSourceCargos
    dataColumn;
    constructor( @Inject(MAT_DIALOG_DATA) public data: any) {
        this.dataSourceServicios = data.servicios;
        this.dataSourceCargos = data.cargos;
        //console.log("data", data);
        this.dataColumn = [{
                    key: 'nombre',
                    title: 'Tipo'
                },
                {
                    key: 'monto',
                    title: 'Monto'
                }]
    }

}



